import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import { GRAY_SCALE, PRIMARY } from "../../../style/GlobalStyle";

const Block = styled.div`
  .contents-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: sticky;
    top: 0;
    .image {
      width: 100vw;
      height: 100vh;
      background: url(image/pipe-a-WQTwsgq_mxg-unsplash.jpg) no-repeat;
      background-position: bottom;
      background-size: 100% 100%;
    }
    .box {
      width: 50vh;
      height: 50vh;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      background: rgba(255, 255, 255, 0.9);
      font-size: 2em;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .greeting {
      font-size: 2.5em;
      font-weight: bold;
      width: 100%;
      opacity: 0;
      transform: scale(140%);
    }
    .cover-slide-area {
      width: 100%;
      margin-top: 40px;
      .items {
        position: relative;
        height: 80px;
        overflow: hidden;
        opacity: 0;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 100%;
      }
      span {
        height: 100%;
        width: 100%;
        text-align: center;
        position: absolute;
        background: #fff;
        font-size: 1.5em;
        display: block;
        line-height: 80px;
      }
      .border-top {
        display: block;
        height: 4px;
        width: 100%;
        background: ${PRIMARY.grade2};
        position: absolute;
        top: -4px;
      }
    }
    .name {
      margin-top: 200px;
      font-size: 1.5em;
      opacity: 0;
      transition: 0.4s;
    }
  }
  .empty {
    height: ${window.innerHeight * 3 + "px"};
  }
`;
const ScaleUpBackgrund = () => {
  const targetRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const targetRefEl = targetRef.current as HTMLElement;
    const imageEl = targetRefEl.querySelector(".image") as HTMLElement;
    const boxEl = document.querySelector(".box") as HTMLElement;

    const eventFunction = () => {
      const targetRect = targetRef.current?.getBoundingClientRect() as DOMRect;
      const relativeTop = targetRect?.top > 0 ? 0 : targetRect?.top;
      const windowHeight = window.innerHeight;
      // console.log("relativeTop", relativeTop);
      let ratio = 1.4;
      interface playerProps {
        start: number;
        end: number;
        hold?: number;
        el: HTMLElement;
        f: (el: HTMLElement, playerPer: number) => void;
      }
      const percentValue = Math.abs(
        Math.round((relativeTop / (targetRect.height - windowHeight / 2)) * 100)
      );
      const per = percentValue * ratio > 100 ? 100 : percentValue * ratio;
      const player = ({ start, end, el, hold, f }: playerProps) => {
        if (true) {
          const range = end - start;
          const progressValue = per - start;
          const playerPer =
            (progressValue / range) * 100 > 100
              ? 100
              : (progressValue / range) * 100 < 0
              ? 0
              : (progressValue / range) * 100;
          f(el, playerPer);
        }
      };
      // true
      // relativeTop < 0
      //상대위치가 0보다 작을것 (화면상에 진행될수록 -value)
      // relativeTop > -targetRect.height
      //상대위치가 높이 - 화면 절반크기보다 클 것. (스크롤이 끝나는 지점을 화면의 절반 더 빨리 잡음)
      //요약: 해당 엘리먼트가 화면이 보이는 시점부터 - 엘리먼트의 끝지점 전까지(화면절반 일찍끝남)
      //안녕하세요
      player({
        start: 0,
        end: 100,
        el: imageEl,
        f: (el, playerPer) => {
          el.style.opacity = `${playerPer}%`;
          el.style.transform = `scale(${200 - playerPer}%)`;
        },
      });
      player({
        start: 0,
        end: 100,
        el: boxEl,
        f: (el, playerPer) => {
          el.style.opacity = `${playerPer}%`;
          el.style.transform = `scale(${500 - playerPer * 4}%)`;
        },
      });
    };
    window.addEventListener("scroll", eventFunction);
    return () => {
      window.removeEventListener("scroll", eventFunction);
    };
  }, []);
  return (
    <Block ref={targetRef}>
      <div className="contents-area">
        <div className="image"></div>
        <div className="box">감사합니다.</div>
      </div>
      <div className="empty"></div>
    </Block>
  );
};

export default ScaleUpBackgrund;
